import React, { useState, useEffect } from 'react';
import { Button, Grid, Box, CircularProgress, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import VoiceAnimation from '../assets/vapiTalking.gif'; // Assuming this is your GIF component
import Vapi from '@vapi-ai/web';

const vapi = new Vapi('e6cd20a5-8cd5-463b-a336-8637a1c7f98d');

export default function VapiAssistant({ setIsUserSpeaking }) {
    const [callStatus, setCallStatus] = useState('inactive');
    const [openFeedback, setOpenFeedback] = useState(false);

    const startCall = async () => {
        setCallStatus('loading');
        await vapi.start('1bf1e306-3e0a-4dfc-a826-bf6490d2da98');
        setCallStatus('active');
    };

    const endCall = () => {
        setCallStatus('inactive');
        vapi.stop();
        setOpenFeedback(true);
    };

    useEffect(() => {
        // Here, replace with real event listeners and state updates
        const timer = setTimeout(() => {
            setIsUserSpeaking(true);
            setTimeout(() => setIsUserSpeaking(false), 2000);
        }, 1000);
        return () => clearTimeout(timer);
    }, [setIsUserSpeaking]);

    const handleCloseFeedback = () => {
        setOpenFeedback(false);
    };

    return (
        <Box p={2}>
            <Grid container spacing={2} alignItems="center" justifyContent="center">
              {callStatus == 'active' && <img src={VoiceAnimation}/>}
                <Grid item xs={12}>
                    {callStatus === 'active'}
                    {callStatus === 'loading' && <CircularProgress />}
                    {callStatus === 'inactive' && <Button variant="contained" color="primary" onClick={startCall}>Start Call</Button>}
                    {callStatus === 'active' && <Button variant="contained" color="secondary" onClick={endCall}>End Call</Button>}
                </Grid>
            </Grid>
            <Dialog open={openFeedback} onClose={handleCloseFeedback}>
                <DialogTitle>Feedback</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        How was your call experience?
                    </DialogContentText>
                    {/* Here add your form or input elements */}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseFeedback} color="primary">
                        Submit
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
}
